import {Pipe, PipeTransform} from "@angular/core";
import {Task} from "../../../shared/interfaces";

@Pipe({
  name: 'completedTasks',
  pure: false
})
export class CompletedTasksPipe implements PipeTransform {
  transform(tasks: Task[], filter: 'completed' | string): Task[] {
    if (!tasks) return [];
    if (tasks && filter === 'completed') {
      return tasks.filter((task: Task) => task.completed);
    } else {
      return tasks;
    }
  }
}
