import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {ReactiveFormsModule} from '@angular/forms';
import {RouterModule} from '@angular/router';
import {RemaindersPageComponent} from "./remainders-page/remainders-page.component";
import {CompletedTasksPipe} from "./pipes/completed-tasks.pipe";
import {TasksSharedModule} from "../tasks/shared/tasks-shared.module";

@NgModule({
  declarations: [
    RemaindersPageComponent,
    CompletedTasksPipe
  ],
  imports: [
    CommonModule,
    ReactiveFormsModule,

    TasksSharedModule,

    RouterModule.forChild([
      {path: '', component: RemaindersPageComponent},
    ]),
  ],
})
export class RemaindersModule {
}
